@import url('https://fonts.googleapis.com/css2?family=Kdam+Thmor+Pro&family=Poppins:wght@600&display=swap');
body{
  background:rgb(241, 241, 241);
  transition:.45s;

}
.App {
  text-align: center;
  width:100%;
  height:100%;
  display:flex;
  justify-content: center;
  align-items: center;
  flex-wrap:wrap
  
}
*, *::before, *::after{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

.countriesContainer{
display:flex;
justify-content: center;
align-items: center;
flex-direction:column;
flex-wrap:wrap;
width:100vw;
}
.rows{
 display:grid;
 grid-template-columns:repeat(3, auto);
 row-gap:10px;

 width:100%;
 margin-bottom:5px;
}
.flag{
  width:120px;
  border:1px solid rgba(255,255,255,0.5);
  border-radius: 7px;
  margin-top:5px
}
.countryCard{
  background:rgb(235,235,235);
  margin:10px;
  width:460px;
  height:240px;
  border-radius:8px;
  transition:0.3s;
  cursor:pointer;
  border:1px solid white;
  box-shadow:0 0 10px rgba(0,0,0,0.3);
  position: relative
}
.countryCard:hover{
  box-shadow: 0 0 2px rgba(0,0,0,0.3);
  transform:scale(1.01);
  background:rgb(245, 245, 245);
}

.population{
  background:rgb(33, 33, 200);
 color:white;
}
.recovered{
  background:rgb(2, 146, 45);
  color:white
}
.data{
  font-family:sans-serif;
  transform:scale(0.85);
  height:55px;
  border-radius:4px;
  padding:4px;
  color:white;
}
.todayCases{
  background:orangered;
}
.todayRecovered{
  background:rgb(196, 196, 56);
}
.deaths{
  background:rgb(221, 31, 31);
}
.tests{
  background:rgb(152, 152, 245)
}
.title{
  font-family:'Poppins',sans-serif
}
.name{
  font-family:'Poppins',sans-serif;
  color:black;
}
.tryAgain{
  font-family:'Montserrat',sans-serif;
  color:white;
}
.noResultsH1{
  color:white;
  font-family:'Poppins',sans-serif
}
.results{
  font-family:'Montserrat',sans-serif
}
.filterInput{
  background: rgba( 255, 255, 255, 0.1 );
  backdrop-filter: blur( 4px );
border-radius:3px;
border:1px solid black;
cursor:pointer;
text-align:center;
transform:scale(1.1);
outline:none
}
.filterInput::placeholder{
  text-align: center;
}
@media (max-width:500px){
  .countryCard{
    transform:scale(0.85);
    margin:0 auto
  }
}
@media (max-width:450px){
  .countryCard{
    transform:scale(0.75);
  }
}
.toggle{
  background:rgb(0, 42, 255);
  padding: 3px 7px;
  color:white;
  border:none;
  border-radius:2px;
  cursor:pointer;
  transition:0.3s;
  position:absolute;
  left:58%;
  top:3%;



}
.toggle:hover{
  background:rgb(39, 74, 246)
}
.toggleDarkMode{
  font-family:sans-serif;
  position:absolute;
  top:5%;
  left:3%;
  width:10%;
  height:40px;
  background:rgb(31, 31, 31);
  border-radius:50px;
  box-shadow: 0 0 15px rgb(31,31,31);
  color:white
}
.toggleHandler{
  width:30px;
  height:30px;
  background:rgb(242, 242, 242);
  border-radius:50%;
  cursor:pointer;
  position:relative;
  top:5px;
  transition:0.45s
}